/* eslint-disable react-hooks/exhaustive-deps */
import Dialog from "@material-ui/core/Dialog";
import MuiDialogContent from "@material-ui/core/DialogContent";
import MuiDialogTitle from "@material-ui/core/DialogTitle";
import IconButton from "@material-ui/core/IconButton";
import { createMuiTheme, createStyles, Theme, ThemeProvider, withStyles, WithStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import CloseIcon from "@material-ui/icons/Close";
import btnPlay from "assets/images/scrollVideo/btnVideo.png";
import LazyImage from "libs/lazy-load/LazyImage";
// import poster from "assets/images/scrollVideo/poster.jpg";
import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import { useLocalize } from "store/actions/localizeAction";
import useWindowSize from "utils/resizeViewport";
import "./ScrollVideo.scss";

const WOW = require("wowjs");
const poster = 'https://gameloft-for-brands.s3-ap-southeast-1.amazonaws.com/assets/poster.jpg'
const styles = (theme: Theme) =>
  createStyles({
    root: {
      margin: 0,
      padding: theme.spacing(2),
    },
    closeButton: {
      position: "absolute",
      right: theme.spacing(1),
      top: theme.spacing(1),
      color: theme.palette.grey[500],
    },
  });

export interface DialogTitleProps extends WithStyles<typeof styles> {
  id: string;
  children: React.ReactNode;
  onClose: () => void;
}

const DialogTitle = withStyles(styles)((props: DialogTitleProps) => {
  const { children, classes, onClose, ...other } = props;
  return (
    <MuiDialogTitle disableTypography className={classes.root} {...other} style={{ color: "#424242" }}>
      <Typography variant="h6">{children}</Typography>
      {onClose ? (
        <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
          <CloseIcon />
        </IconButton>
      ) : null}
    </MuiDialogTitle>
  );
});

const DialogContent = withStyles((theme: Theme) => ({
  root: {
    padding: theme.spacing(2),
  },
}))(MuiDialogContent);

export interface ScrollVideoDataType {
  label: {
    intro: string;
    title: string;
    paragraph1: string;
    paragraph2: string;
  };
  video: {
    mimeType: string;
    url: string;
  };
  poster?: any;
  description?: string;
}

const ScrollVideo: React.FC = () => {
  const location = useLocation();
  const data = useLocalize<ScrollVideoDataType>("scrollVideo");

  useEffect(() => {
    const wow = new WOW.WOW({
      live: false,
    });
    wow.init();
  }, []);
  const [open, setOpen] = useState(false);
  const [width] = useWindowSize();

  const checkSizeDevice = () => {
    return width < 1550;
  };

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const darkTheme = createMuiTheme({
    palette: {
      type: "dark",
    },
  });

  const handleLoad = () => {
    setTimeout(() => {
      let elm = document.getElementById("video-poster");
      if (location.hash === "#video-post" && elm) {
        elm.scrollIntoView({
          behavior: "smooth",
          block: "center",
        });
      }
    }, 800);
  };

  return (
    <section className="com-scrollVideo">
      <div className="label">{data?.label.title}</div>
      <div className="wrapper">
        <div className="title">{data?.label.intro}</div>
      </div>

      <div className="wrapper-video">
        <LazyImage id="video-poster" src={data?.poster || poster} alt="poster" onLoadProp={handleLoad}></LazyImage>
        <img id="btnPlay" src={btnPlay} alt="" onClick={handleClickOpen} />
      </div>

      {
        data?.label?.paragraph1 && <div className="description">
          <p>{data.label.paragraph1}</p>
          <p>{data.label.paragraph2}</p>
        </div>
      }

      <ThemeProvider theme={darkTheme}>
        <Dialog onClose={handleClose} aria-labelledby="customized-dialog-title" open={open} maxWidth={checkSizeDevice() ? "md" : "lg"}>
          <DialogTitle id="customized-dialog-title" onClose={handleClose}>
            .
          </DialogTitle>
          <DialogContent dividers>
            <video loop autoPlay controls muted preload="none" >
              <source src={data?.video.url} type={data?.video.mimeType} />
            </video>
          </DialogContent>
        </Dialog>
      </ThemeProvider>
    </section>
  );
};

export default ScrollVideo;
